.inputWrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

.input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: border-color 0.3s ease;
}

.input:focus {
    outline: none;
    border-color: #007bff;
}

.superInput {
    border: 1px solid #007bff;
}

.errorInput {
    border-color: red;
}

.errorInput:focus {
    border-color: darkred;
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 5px;
}
