.button {
    border-radius: 0px;
    text-align: center;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* Стили для состояния disabled */
.disabled {
    background-color: #e0e0e0;
    color: #a0a0a0;
    cursor: not-allowed;
}

/* Стили для типов кнопок */
.default {
    background-color: #007bff;
    color: white;
}

.secondary {
    background-color: #6c757d;
    color: white;
}

.red {
    background-color: #cc1439;
    color: white;
}

/* Hover-состояния для кнопок */
.default:hover {
    background-color: #0056b3;
}

.secondary:hover {
    background-color: #5a6268;
}

.red:hover {
    background-color: #b31232;
}

/* Active-состояния для кнопок */
.default:active {
    background-color: #004085;
}

.secondary:active {
    background-color: #343a40;
}

.red:active {
    background-color: #8a1029;
}

/* Стили для заблокированной кнопки */
.button:disabled {
    background-color: #e0e0e0;
    color: #a0a0a0;
    cursor: not-allowed;
}
